let app = require("@/config");
export default {
	data() {
		return {
			preAge: '',
			age: '',
			currentUserGuid: '',
			tip: ''
		};
	},
	onLoad() {
		this.currentUserGuid = app.userInfo.token;
		uni.getStorage({
			key: 'userInfo',
			success: (res) => {
				this.preAge = res.data.Grade;
			}
		});
	},
	methods: {
		Modifyage() {
			let currentUserGuid = this.currentUserGuid;
			let grade = this.age;
			uni.post("/uc/ModifyGrade", {
				currentUserGuid,
				grade
			}, msg => {
				if (msg.success) {
					this.tip = msg.msg;
					uni.showMessage(this.tip);
					this.preage = this.age;
				} else {
					this.tip = msg.msg;
					uni.showMessage(this.tip);
				}
			});
		}
	}
}
